        // Se deu certo, fecha o modal e redireciona
        setShowModal(false);
        // Pequeno delay para usu√°rio ver a anima√ß√£o de sucesso (opcional)
        router.push(`/leitor/${mangaData.id}/${selectedChapter}`);
